footer {
  width: 100%;
}

ul {
  list-style-type: none;
}

.footer {
  background-color: #24262b;
  color: white;
  padding: 20px 0px 20px 0px;
  text-align: left;
}

.footer-container {
  box-sizing: border-box;
  max-width: 1170px;
  margin: auto;
}

.footer-row {
  /* display: flex;
    flex-wrap: wrap; */
  margin-bottom: 10px;
  text-align: center;


}

.footer-col {
  width: 25%;
  padding: 0px 15px;


}

.footer-col h4 {
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  font-size: 18px;
  color: #e0e0e0;
  text-transform: capitalize;
  margin-bottom: 30px;

}

.footer-a {
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  /* font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif; */
  font-size: 14px;
  font-style: normal;
  color: rgb(139, 139, 160);
  text-decoration: none;
  line-height: 2;

}

.copyright {
  text-align: center;
  color: #558a9e;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  margin: 0px;
}

h4 {

  color: #ffffff;
}